import axios from "axios";
import { apiUrl, headers } from "@/services/config.js";

export const error = () => {
  alert(`Error! \n${error.code}. \n${error.message}`);
};
export const getRequest = async ([serviceName, api], parameter, callback) => {
  const module = await import(`@/services/${serviceName}.js`);
  const service = module[api];
  const url = service();

  let queryString = "";
  if (parameter) {
    for (const prop in parameter) {
      queryString = `${queryString}${prop}=${parameter[prop]}`;
    }
  }
  console.log("getRequest", [serviceName, api], queryString);
  console.log(`${apiUrl}${url}?${queryString}`);

  axios({
    headers,
    method: "GET",
    url: `${apiUrl}${url}?${queryString}`,
    mode: "no-cors",
  })
    .then((response) => {
      console.log(response.data);
      console.log("================================");
      callback(response.data);
    })
    .catch((error) => {
      alert(`Error! \n${error.code}. \n${error.message}`);
    });
};

export const postRequest = async ([serviceName, api], body, callback) => {
  const module = await import(`@/services/${serviceName}.js`);
  const service = module[api];
  const url = service();

  console.log("postRequest", [serviceName, api], body);
  console.log(`${apiUrl}${url}`);

  axios({
    headers,
    method: "POST",
    url: `${apiUrl}${url}`,
    data: body,
    mode: "no-cors",
  })
    .then((response) => {
      const responseData = response.data;
      if (responseData.code === "S00") {
        console.log(response);
        console.log("================================");
        callback(responseData.result);
      } else {
        alert(`Error Code ${responseData.code}. \n${responseData.detailmsg}`);
        console.error(responseData);
      }
    })
    .catch((error) => {
      alert(`Error Code ${error.code}. \n${error.message}`);
    });
};
export const deleteRequest = async ([serviceName, api], body, callback) => {
  const module = await import(`@/services/${serviceName}.js`);
  const service = module[api];
  const { url, data } = service(body);

  console.log("deleteRequest", [serviceName, api], body);
  console.log("url - ", `${apiUrl}${url}`);

  axios({
    headers,
    method: "DELETE",
    url: `${apiUrl}${url}`,
    data,
    mode: "no-cors",
  })
    .then((response) => {
      const responseData = response.data;
      if (responseData.code === "S00") {
        callback(responseData.result);
      } else {
        alert(`Error Code ${responseData.code}. \n${responseData.detailmsg}`);
        console.error(responseData);
      }
    })
    .catch((error) => {
      alert(`Error Code ${error.code}. \n${error.message}`);
    });
};
